import { firestoreAction } from "vuexfire";
import db from "../firebase";

const state = {
  pokemons: [],
  mypokemons: []
};
const getters = {
  pokemons: state => {
    return state.pokemons;
  },
  pokemonsShow: state => {
    return state.pokemons.map(pokemon => ({
      name: pokemon.name,
      id: pokemon.id,
      userId: pokemon.userId,
      img: pokemon.img,
      description: pokemon.description
    }));
  },
  MypokemonsShow: state => {
    return state.mypokemons.map(pokemon => ({
      name: pokemon.name,
      id: pokemon.id,
      userId: pokemon.userId,
      img: pokemon.img,
      description: pokemon.description
    }));
  }
};
const actions = {
  initPokemons: firestoreAction(({ bindFirestoreRef }) => {
    bindFirestoreRef("pokemons", db.collection("pokemons"));
  }),
  initMyPokemons: firestoreAction(({ bindFirestoreRef }, userId) => {
    //Denna 칛r egentligen "on칬dig" f칬r v친rat 칛ndam친l d친 vi 칛nd친 ska ha alla pokemons f칬r att kika p친 men i andra projekt kan denna vara bra.
    bindFirestoreRef(
      "mypokemons",
      db.collection("pokemons").where("userId", "==", userId)
    ); //Vill man ta bara "mina" pokemon fr친n alla arrayen s친 kan man anv칛nda pokemon.filter(pokemon=> pokemon.userId== V친ratID) t.ex
  }),

  updatePokemon({ commit }, Pokemon) {
    console.log(Pokemon + "Uppdateras");
    db.collection("pokemons")
      .doc(Pokemon.id)
      .update(Pokemon);
  },
  removePokemon({}, PokemonId) {
    console.log(PokemonId + " tas bort");
    db.collection("pokemons")
      .doc(PokemonId)
      .delete();
  },
  createPokemon({}, Pokemon) {
    Pokemon.uploadTime = Date.now();
    console.log(Pokemon + " skapas");
    db.collection("pokemons").add(Pokemon);
  }
};

export default {
  namespaced: true,
  state,
  actions,
  getters
};
